import React, { useCallback, useEffect, useState } from "react";
import ReactDOM from "react-dom";
import IconButton from "@mui/material/IconButton";
import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";
import OutlinedInput from "@mui/material/OutlinedInput";
import InputAdornment from "@mui/material/InputAdornment";
import TextField from "@mui/material/TextField";
import { green, red } from "@mui/material/colors";
import Icon from "@mui/material/Icon";
import "./styles.css";
import { Box } from "@mui/material";
import NumberFormatCustom from "./NumberFormatCustom";

function App() {
  const [amount, setAmount] = useState(10000);
  const [splitPrices, setSplitPrices] = useState([92.67, 1020.89, 3.56]);
  const [targetSplits, setTargetSplits] = useState([14, 21, 65]);
  const [currentSplits, setCurrentSplits] = useState([6764, 14292, 52941]);
  const [totalPercent, setTotalPercent] = useState(0);
  const [currentTotal, setCurrentTotal] = useState(0);
  const [newInvestment, setNewInvestment] = useState(0);
  useEffect(
    () => setTotalPercent(targetSplits.reduce((acc, c) => acc + Number(c), 0)),
    [targetSplits]
  );
  useEffect(
    () => setCurrentTotal(currentSplits.reduce((acc, c) => acc + Number(c), 0)),
    [currentSplits]
  );

  const onSplitCostChanges = useCallback(
    (id) => (e) => {
      const { value } = e.target;
      const newSplitPrices = [...splitPrices];
      newSplitPrices[id] = value;
      setSplitPrices(newSplitPrices);
    },
    [splitPrices]
  );
  const onSplitPercentChanges = useCallback(
    (id) => (e) => {
      const newtargetSplits = [...targetSplits];
      newtargetSplits[id] = Number(e.target.value);
      setTargetSplits(newtargetSplits);
    },
    [targetSplits]
  );
  const onTargetSplitValueChanges = useCallback(
    (id) => (e) => {
      if (amount > 0) {
        const { value } = e.target;
        const newtargetSplits = [...targetSplits];
        newtargetSplits[id] = Number((value * 100) / amount);
        setTargetSplits(newtargetSplits);
      }
    },
    [targetSplits, amount]
  );
  const onCurrentSplitValueChanges = useCallback(
    (id) => (e) => {
      const { value } = e.target;
      const newtargetSplits = [...currentSplits];
      newtargetSplits[id] = value;
      setCurrentSplits(newtargetSplits);
    },
    [currentSplits]
  );
  const handleAddtargetSplits = useCallback(() => {
    setTargetSplits((olstargetSplits) => [...olstargetSplits, 0]);
  }, []);

  const handleRemovetargetSplits = useCallback((idx) => {
    setTargetSplits((olstargetSplits) => {
      olstargetSplits.splice(idx, 1);
      return [...olstargetSplits];
    });
  }, []);
  const handleAmountChange = useCallback(
    (e) => setAmount(Number(e.target.value)),
    []
  );
  const handleNewInvestmentChange = useCallback(
    (e) => setNewInvestment(Number(e.target.value)),
    []
  );
  return (
    <div className="App">
      <h1>Portfolio Rebalance Calculator</h1>
      <Box
        className="split-section"
        sx={{
          "& > :not(style)": {
            m: 1
          }
        }}
      >
        <TextField
          label="Starting Amount"
          value={amount}
          onChange={handleAmountChange}
          name="numberformat"
          id="formatted-numberformat-input"
          InputProps={{
            inputComponent: NumberFormatCustom
          }}
          variant="standard"
        />
        <TextField
          label="New Investment"
          value={newInvestment}
          onChange={handleNewInvestmentChange}
          name="numberformat"
          id="newInvetment"
          InputProps={{
            inputComponent: NumberFormatCustom
          }}
          variant="standard"
        />
      </Box>
      <Box
        className="split-section"
        sx={{
          "& > :not(style)": {
            m: 1
          }
        }}
      >
        <FormControl fullWidth sx={{ m: 1 }}>
          <InputLabel htmlFor="total">Total</InputLabel>
          <OutlinedInput
            id="total"
            size="small"
            disabled
            value={totalPercent}
            endAdornment={<InputAdornment position="end">%</InputAdornment>}
            label="Total"
            error={totalPercent !== 100}
          />
        </FormControl>
        <FormControl fullWidth sx={{ m: 1 }}>
          <InputLabel htmlFor="Total(£)">Total(£)</InputLabel>
          <OutlinedInput
            size="small"
            id="Total(£)"
            label="Total(£)"
            disabled
            value={(amount * totalPercent) / 100}
            name="numberformat"
            inputComponent={NumberFormatCustom}
            variant="standard"
          />
        </FormControl>
        <FormControl fullWidth sx={{ m: 1 }}>
          <InputLabel htmlFor="balance">Current Total</InputLabel>
          <OutlinedInput
            size="small"
            id="balance"
            label="Current Total"
            disabled
            value={currentTotal}
            name="numberformat"
            inputComponent={NumberFormatCustom}
            variant="standard"
          />
        </FormControl>
      </Box>
      {targetSplits.map((split, i) => {
        const splitInAmt = Number((amount * split) / 100);
        const splitCost = splitPrices[i];
        const targetAmountFromTotalCurrent = (currentTotal * split) / 100;
        const newInvestmentSplit = (newInvestment * targetSplits[i]) / 100;
        const currentSplit = currentSplits[i] ? Number(currentSplits[i]) : 0;
        const rebalanceAmt =
          targetAmountFromTotalCurrent - currentSplit + newInvestmentSplit;
        const rebalanceQty = Number(rebalanceAmt / splitCost);
        const side = rebalanceQty > 0 ? "Buy" : "Sell";
        const residueAmt =
          (rebalanceQty - Number(Math.floor(rebalanceQty))) * splitCost;
        return (
          <Box
            key={i}
            className="split-section"
            sx={{
              "& > :not(style)": {
                m: 1
              }
            }}
          >
            <FormControl fullWidth sx={{ m: 1 }}>
              <InputLabel htmlFor="amount">{`Stock-${i + 1}`}</InputLabel>
              <OutlinedInput
                size="small"
                id="amount"
                value={split.toFixed(0)}
                onChange={onSplitPercentChanges(i)}
                endAdornment={<InputAdornment position="end">%</InputAdornment>}
                label={`Stock-${i + 1}`}
                error={totalPercent !== 100}
              />
            </FormControl>
            <FormControl fullWidth sx={{ m: 1 }}>
              <InputLabel htmlFor="cost">{`Price-${i + 1}`}</InputLabel>
              <OutlinedInput
                size="small"
                id="cost"
                value={splitCost}
                onChange={onSplitCostChanges(i)}
                startAdornment={
                  <InputAdornment position="start">£</InputAdornment>
                }
                label={`Price-${i + 1}`}
              />
            </FormControl>
            <FormControl fullWidth sx={{ m: 1 }}>
              <InputLabel htmlFor={`amount${i}`}>£</InputLabel>
              <OutlinedInput
                size="small"
                id={`amount${i}`}
                label="£"
                value={splitInAmt}
                name="numberformat"
                inputComponent={NumberFormatCustom}
                variant="standard"
                onChange={onTargetSplitValueChanges(i)}
              />
            </FormControl>

            <FormControl fullWidth sx={{ m: 1 }}>
              <InputLabel htmlFor={`qty${i}`}>Qty#</InputLabel>
              <OutlinedInput
                size="small"
                disabled
                id={`qty${i}`}
                label="Qty"
                value={Number(splitInAmt / splitCost).toFixed(0)}
              />
            </FormControl>

            <FormControl fullWidth sx={{ m: 1 }}>
              <InputLabel htmlFor={`currntAmount${i}`}>Current£</InputLabel>
              <OutlinedInput
                size="small"
                id={`currntAmount${i}`}
                label="Current£"
                value={Number(currentSplits[i])}
                name="numberformat"
                inputComponent={NumberFormatCustom}
                variant="standard"
                onChange={onCurrentSplitValueChanges(i)}
              />
            </FormControl>
            <FormControl fullWidth sx={{ m: 1 }}>
              <InputLabel htmlFor={`currentPercent${i}`}>Current%</InputLabel>
              <OutlinedInput
                size="small"
                disabled
                id={`currentPercent${i}`}
                label="Current%"
                value={Number(
                  currentSplits[i]
                    ? Number((currentSplits[i] * 100) / currentTotal)
                    : 0
                ).toFixed(0)}
                endAdornment={<InputAdornment position="end">%</InputAdornment>}
              />
            </FormControl>

            <FormControl fullWidth sx={{ m: 1 }}>
              <InputLabel htmlFor={`rebalanceAmount${i}`}>Rebalance</InputLabel>
              <OutlinedInput
                size="small"
                disabled
                id={`rebalanceAmount${i}`}
                label="Rebalance"
                value={Number(rebalanceAmt).toFixed(0)}
                startAdornment={
                  <InputAdornment position="start">£</InputAdornment>
                }
              />
            </FormControl>
            <FormControl fullWidth sx={{ m: 1 }}>
              <InputLabel htmlFor={`rebalanceQty${i}`}>Rebal#</InputLabel>
              <OutlinedInput
                size="small"
                disabled
                id={`rebalanceQty${i}`}
                label="Rebal#"
                value={`${side} ${Math.abs(
                  Math.floor(rebalanceQty)
                )} (£${residueAmt.toFixed(2)})`}
              />
            </FormControl>
            <IconButton
              color="secondary"
              onClick={() => handleRemovetargetSplits(i)}
            >
              <Icon fontSize="small" sx={{ color: red[500] }}>
                remove_circle
              </Icon>
            </IconButton>
          </Box>
        );
      })}

      <IconButton
        color="primary"
        aria-label="add to shopping cart"
        onClick={handleAddtargetSplits}
      >
        <Icon sx={{ color: green[500] }}>add_circle</Icon>
      </IconButton>
    </div>
  );
}

const rootElement = document.getElementById("root");
ReactDOM.render(<App />, rootElement);
